import React, { Component } from 'react';
import axios from 'axios';


function getArrayStr(data){
    var str = "";
    data.map((item, index) => {
        if(index === data.length-1){
            str = str +item.toString();
        }else{
            str = str + item.toString() + ", ";
        }
        }
    );
    return(
        {str: str}
    );
}

function getElkeszitesStr(data){
    
    return(
        data.map((item, index) => {
           return <ul>
                <p>{index+1}. lépés:</p>
                <p>{item.text}</p>
            </ul>
        }
        )
    );
}

function getHozzavalokStr(data){
    return(
        data.map((item) => {
            return(
                <ul>
                    {item.name.toString()}
                    {item.alapanyagok.map((alapanyag) => {
                        return (
                            <ul>
                                {alapanyag.mennyiseg}
                                {" "}
                                {alapanyag.mertekegyseg}
                                {" "}
                                {alapanyag.name}
                            </ul>
                        );
                    })
                }
                    
                </ul>
            )
            }
        )
    );
}


export default class Index extends Component {

  constructor(props) {
      super(props);
      this.state = {recipe: []};
    }
    componentDidMount(){
      axios.get('http://localhost:4000/recipe')
        .then(response => {
          this.setState({ recipe: response.data });
          console.log("WW");
        })
        .catch(function (error) {
          console.log(error);
        })
    }


    render() {
      return (
        <div class="centered" style={{marginTop: 10, marginLeft: 20}}>
        {this.state.recipe.map((rec) => {
            return(<div>
                <h2>{rec.receptCim}</h2>
                <h5>Évszakok: {getArrayStr(rec.evszak).str}</h5>
                <h5>Ünnepek: {getArrayStr(rec.unnep).str}</h5>
                <h5>Diéta: {getArrayStr(rec.dieta).str}</h5>
                <h5>Menü: {getArrayStr(rec.menu).str}</h5>
                <h5>Napszak: {getArrayStr(rec.napszak).str}</h5>
                <h5>Technika: {getArrayStr(rec.technika).str}</h5>

                
                <h5>Elkészítési idő: {rec.elkeszitesiIdo} perc</h5>
                <h5>Kalória: {rec.kaloria} kcal</h5>
                <h5>Hozzávalok: {getHozzavalokStr(rec.hozzavalok)}</h5>
                <h5>Elkészítés: {getElkeszitesStr(rec.elkeszites.step)}</h5>
            </div>);
        })
        }
        </div>
      );
    }
  }