import React, { Component } from 'react';
import NormalInput from "./NormalInput"
import DeleteIcon from '@material-ui/icons/Delete';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import FormGroup from '@material-ui/core/FormGroup';

import Grid from '@material-ui/core/Grid';
import FormLabel from '@material-ui/core/FormLabel';

class Elkeszites extends React.Component{
    render(){
        return (
            <div>
            <FormLabel >{this.props.title}</FormLabel>
            <ul>
            {
            <div>
                {this.props.elkeszites.step.map((lepes, index) => {
                    const tmp = (index + 1) + " lépés:";
                    return (
                        <div>
                            <Grid item xs={12} style={{marginTop: 20, marginLeft: 20, marginRight: 20}}>
                           <FormLabel>{tmp}</FormLabel>
                         
                            <Fab color="secondary"  aria-label="Delete"  size="small"  style={{marginLeft: 20, marginRight: 20}}>
                                <DeleteIcon 
                                    onClick={() => this.props.removeStep(index)}
                                    />
                            </Fab> 
                            <NormalInput 
                                //placeholder="Írd be a lépést"
                                required={true}
                                width="300%"
                                margin="normal"
                                value={lepes.text} 
                                multiline={true}
                                onChange={e => this.props.onStepChange(lepes.id, e)}
                            />
                            </Grid>
                        </div>
                    )
                })}
                 <Grid item xs={12} style={{marginTop: 20, marginLeft: 20, marginRight: 20}}>
                <Fab  
                    aria-label="Add"  
                    size="small" 
                    color="secondary" 
                    onClick={() => this.props.addStep()}
                    >
                    <AddIcon />
                </Fab> 
                </Grid>
                </div>
            }       
        </ul>
        </div>
        )
    }
       
}

                   


export default Elkeszites;
