
import React, { Component } from "react";
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormGroup from '@material-ui/core/FormGroup';
import Checkbox from '@material-ui/core/Checkbox';
import FormLabel from '@material-ui/core/FormLabel';

import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import green from '@material-ui/core/colors/green';

const styles = {
    root: {
      color: '#445a84',
      '&$checked': {
        color:  '#2E3B55',
      },
    },
    checked: {},
  };

class MultipleChoose extends React.Component{
    render(){
        
        const { classes } = this.props;
        const checkboxItems = this.props.labels.map((oneLabel) =>
            <FormControlLabel
            control={<Checkbox
                color="primary"
                 onChange={e => this.props.onChange(oneLabel, e)}/>}
            label={oneLabel}/>
        );
        return (
            <div>
                <FormLabel >{this.props.title}</FormLabel>
                <FormGroup row={true} className="form-group">
                    {checkboxItems}
                </FormGroup>
            </div>
        );
    }
}

MultipleChoose.propTypes = {
    classes: PropTypes.object.isRequired,
  };
  
  export default withStyles(styles)(MultipleChoose);
